///<reference path="../dist/Excalibur.d.ts" />
"use strict";
require('../style/main.less');
var Player = require("./components/player/player");
var game = new ex.Engine({
    width: 800,
    height: 600
});
var player = new Player(100, game.getHeight() - 40, 20, 20, ex.Color.Red);
game.add(player);
game.start();



